import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import { Experience, ExperiencesList } from "../components/experience"
import SEO from "../components/seo"

function Experiences({ data, location }) {
  const { jobs, cv } = data
  return (
    <Layout location={location}>
      <SEO title="Experience and qualifications"/>
      <h1>Experience and qualifications</h1>
      <p>You might also want to have a look at my <a href={cv.publicURL}>CV</a>.</p>
      <ExperiencesList>
        {jobs.nodes.map(({ title, ...props }) => <Experience key={title} title={title} {...props} />)}
      </ExperiencesList>
    </Layout>
  )
}

export default Experiences

export const experiencesQuery = graphql`
  query {
    cv: file(relativePath: { eq: "cv_en.pdf" }) {
      publicURL
    }
    jobs: allJobsYaml {
      nodes {
        title
        isCurrent
        company
        details
        dates
        link
      }
    }
  }
`