import styled from "@emotion/styled"
import { rhythm, THEME_BLUE } from "../utils/typography"
import React from "react"
import logo from "../utils/logos"
import { Tag } from "./tags"

export const ExperiencesList = styled.ul({
  listStyleType: "none",
  borderLeft: "2px solid #CCC",
  paddingLeft: rhythm(2),
  position: "relative",
})

const Summary = styled.li(({ isCurrent }) => ({
  "&:before": {
    content: "''",
    position: "absolute",
    width: rhythm(2 / 3),
    height: rhythm(2 / 3),
    left: rhythm(-1 / 3),
    background: isCurrent ? THEME_BLUE : "black",
    borderRadius: "50%",
    border: "4px solid white",
  },
}))

const Title = styled.h4({
  marginBottom: rhythm(1 / 3),
})

export function Experience({ isCurrent, title, dates, details, link, company }) {
  const Logo = logo(company)
  return (
    <Summary key={title} isCurrent={isCurrent}>
      <Title>{title}, {link ? <a href={link}><Logo height={2 / 3}/></a> : <Logo height={2 / 3}/>}</Title>
      <small>{dates}</small>
      <p>{details}</p>
      <p>More: <Tag tag={company}/></p>
    </Summary>
  )
}